.full-width-modal-section.modal-dialog {
	max-width: 700px;
}

.MuiPaper-elevation4{box-shadow: none;}
.addDrs{background: #f6f8f9;
    padding: 20px;}
    

.select-user-info {
    background: #c8e7ef;
    text-align: center;
    padding: 0px 0px;
    border-radius: 10px;
    overflow: hidden;
    align-items: center;
    border: 1px solid #c8e7ef;
    height: 40px;
    line-height: 38px;
    color: #266e7b;
    font-weight: 500;
}

.remove-selected-user-info {
    float: right;
    background: #83d2e0;
    border: none;
    border-radius: 8px;
    padding: 0px 12px;
    color: #fff;
}